.mainBlock{
    display: flex;
    flex-direction: row;
    /*font-size: 13px;*/
    font-size: 12px;
    letter-spacing: 1.1px;
    font-family: Ubuntu,sans-serif;
    font-weight: 400;
    line-height: 1.0;
}

.categoryList{
    display: flex;
    justify-content: center;
    height: 25px;
    text-transform: lowercase;
}
.categoryList:first-child {
    text-transform: capitalize;
}

/*  LEFT   */
.left_block{
    margin-right: 38px;
    padding-top: 10px;
}
.left_block:hover{
    color: black;
    cursor: pointer;
}
.leftLink{
    padding-bottom: 3px;
    border-bottom: black 2px solid;
}
.leftLink:hover{
    cursor: pointer;
    width: fit-content;
    color: black;
    padding-bottom: 7px;
    border-bottom: white 2px solid;
}

/*  RIGHT   */
.right_block{
    margin: 0 10px 0 0;
    padding: 10px 13px;
}
.right_block:hover{
    padding-bottom: 100px;
    bottom: 50px;
}
.rightLink{
    padding-bottom: 3px;
    border-bottom: black 2px solid;
    text-decoration: none;
}
.rightLink:hover{
    width: fit-content;
    cursor: pointer;
}